"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";

export default function FrameworksLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const pathname = usePathname();

    const links = [
        { href: "/docs/frameworks", label: "Overview" },
        { href: "/docs/frameworks/nextjs", label: "Next.js" },
        { href: "/docs/frameworks/vite", label: "Vite" },
    ];

    return (
        <div className="flex flex-col md:flex-row gap-8 py-8 px-4 max-w-7xl mx-auto">
            <aside className="w-full md:w-64 shrink-0">
                <nav className="flex flex-col gap-2">
                    <h3 className="font-semibold text-white mb-2 px-3">Frameworks</h3>
                    {links.map((link) => (
                        <Link
                            key={link.href}
                            href={link.href}
                            className={cn(
                                "px-3 py-2 rounded-lg text-sm transition-colors",
                                pathname === link.href
                                    ? "bg-white/10 text-white font-medium"
                                    : "text-white/60 hover:text-white hover:bg-white/5"
                            )}
                        >
                            {link.label}
                        </Link>
                    ))}
                </nav>
            </aside>
            <main className="flex-1 min-w-0">
                {children}
            </main>
        </div>
    );
}
